@import url('https://fonts.googleapis.com/css?family=Macondo');

.App {

    border: 2px solid black;
    text-align: center;

    background-image: url(../img/background.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center top;

    color: #000000;
    margin: 0px;
    padding: 0px;
    min-height: 700px;
}

.App-header {
  padding-top: 10px;
  padding-bottom:10px;
  color: black;
  font-size: 40px;
}

.FancyText {
    font-family: 'Macondo', cursive;
    font-weight: normal;
}

.button {
     background: #d1c99f;
    background-image: -webkit-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -o-linear-gradient(top, #d1c99f, #e6d565);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#d1c99f), to(#e6d565));
    background-image: linear-gradient(to bottom, #d1c99f, #e6d565);
    border-radius: 6px;
    text-shadow: 2px 2px 7px #666666;
    -webkit-box-shadow: 1px 1px 3px #575442;
    box-shadow: 1px 1px 3px #575442;
    color: #ffffff;
    font-size: 20px;
    padding: 10px 20px 10px 20px;
    border: solid #a38968 2px;
    text-decoration: none;
}


